<template>
    <div class="main">
        <div class="roleManagement">
            <div class="searchA">
                <div class="add">
                    <el-button size="small" type="primary" icon="el-icon-plus" @click="addBtn">新增</el-button>
                </div>
            </div>
            <div class="home_table">
                <el-table :data="tableData" border :max-height="tableHeight" style="width: 100%" size="medium" v-loading="isLoading">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" v-if="item.title == '状态'" width='150'>
                            <template slot-scope="scope">
                                <span v-if="scope.row.disabled == 0" style="color: #409EFF;">启用</span>
                                <span v-else-if="scope.row.disabled == 1" style="color: #F56C6C;">禁用</span>
                            </template>
                        </el-table-column>
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :width="item.width" :show-overflow-tooltip='true' v-else>
                        </el-table-column>
                    </template>

                    <el-table-column prop="address" label="操作" fixed="right" width='150'>
                        <template slot-scope="scope">
                            <el-button @click="editBtn(scope.row)" type="text" size="small">编辑</el-button>
                            <el-button @click="delBtn(scope.row.id)" type="text" size="small" style="color: #ff2b2b" v-if='defaulted==1'>删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!-- 新增编辑弹窗 -->
            <el-dialog :title="title" :visible.sync="dialogVisible" v-dialogDrag width="700px" v-if="dialogVisible" :close-on-click-modal="false">
                <div style="padding-left:20px;">
                    <el-form :model="formData" ref="formData" :rules="rule" status-icon label-position="top" label-width="100px" :inline="true">
                        <div class="dialogInput" style="display:flex">
                            <div class="padd" style="margin-right: 10px;">
                                <el-form-item label="角色名称" prop="role_name">
                                    <el-input v-model="formData.role_name" placeholder="请选择角色名称" size="small" style="width:100%;"></el-input>
                                </el-form-item>
                            </div>
                            <div class="padd" style="margin-right: 10px;">
                                <el-form-item label="角色状态">
                                    <el-switch v-model="formData.disabledS">
                                    </el-switch>
                                </el-form-item>
                            </div>
                        </div>
                        <div class="dialogInput" style="display:flex">
                            <div class="padd" style="margin-right: 10px;">
                                <el-form-item label="角色描述" prop="remarks" style="width:100%;">
                                    <el-input v-model="formData.remarks" placeholder="请输入角色描述（60个字符以内）" size="small" style="width:100%;"></el-input>
                                </el-form-item>
                            </div>
                        </div>
                        <div class="dialogInput">
                            <div class="padd">
                                <div class="paddTit">资源权限</div>
                                <div class="tree_main">
                                    <el-form-item>
                                        <el-tree :data="nodesData" show-checkbox check-on-click-node node-key="id" ref="tree" :default-checked-keys='rolesData' default-expand-all :props="defaultProps">
                                        </el-tree>
                                    </el-form-item>
                                </div>
                            </div>

                        </div>

                        <div style="text-align: center;margin-top: 20px;">
                            <el-button type="primary" class="btn" @click="submitForm('formData')">确 定</el-button>
                        </div>

                    </el-form>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import {
    hashApi,
    roleOrgListApi,
    roleOrgRangeListApi,
    roleOrgAddApi,
    roleOrgEditApi,
    roleOrgRolesApi,
    roleOrgDelApi
} from "@/api/index";
export default {
    data() {
        return {
            formData: {
                role_name: '',
                remarks: "",
                role_type: "",
                node_ids: "",
                disabledS: true
            },
            method_auth: {
                actAdd: 0,
                actEdit: 0,
                actDelete: 0,
                setState: 0,
            },
            tableHeight: window.innerHeight - 180,
            title: "",
            listId: "",
            hash: "",
            dialogVisible: false,
            tableHead: [
                { title: "编号", prop: "id", width: '100' },
                { title: "角色名", prop: "role_name", width: '300' },
                { title: "角色类型", prop: "role_type", width: '300' },
                { title: "描述", prop: "remarks" },
                { title: "状态", prop: "disabled" },
            ],
            nodesData: [],
            tableData: [],
            rule: {
                role_name: [
                    { required: true, message: '请选择角色名称', trigger: 'blur' },
                ],
                role_type: [
                    { required: true, message: '请选择权限范围', trigger: 'change' },
                ],
            },
            defaultProps: {
                children: 'children',
                label: 'node_name',
                disabled: 'defaulted',
            },
            startOption: {},
            rolesData: [],
            defaulted: "",
            isLoading: false
        };
    },
    mounted() {
        this.getList();

    },
    methods: {

        //重置对话框表单
        resetForm() {
            this.formData = {
                role_name: '',
                remarks: "",
                role_type: "",
                node_ids: "",
            };
            this.nodeData = [];
        },
        // 点击新增
        addBtn() {
            this.title = '新增';
            this.dialogVisible = true;
            this.resetForm();
            this.getInfoData();
            this.getHash();
        },

        // 点击编辑
        editBtn(e) {
            this.title = '编辑';
            this.dialogVisible = true;
            this.formData.role_name = e.role_name;
            this.formData.role_type = e.role_type;
            this.formData.remarks = e.remarks;
            this.formData.disabledS = (e.disabled == 0 ? true : false);
            this.listId = e.id;
            this.getInfoData();
            this.getRolesData(e.id);
            this.getHash();
        },

        // 新增编辑提交
        submitForm(formName) {

            this.$refs[formName].validate(async (valid) => {
                if (valid) {

                    let datas = {
                        disabled: this.formData.disabledS == true ? 0 : 1,
                        role_name: this.formData.role_name,
                        role_type: this.formData.role_type,
                        node_ids: this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys()).join(','),
                        remarks: this.formData.remarks,
                        // role_grade: this.dictionary_value,
                        hash: this.hash,
                    }
                    if (this.title == '新增') {
                        var urls = roleOrgAddApi;
                    } else if (this.title == '编辑') {
                        var urls = roleOrgEditApi;
                        datas.id = this.listId;
                    }
                    const res = await this.axios.post(urls, datas)
                    if (res.data.code == 1) {
                        this.dialogVisible = false;
                        this.getList();
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: "error"
                        });
                    }
                    this.getHash();
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },

        //删除
        async delBtn(id) {
            this.$confirm("是否删除该条数据?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消"
            })
                .then(async () => {
                    const datas = {
                        id: id,
                    };
                    const res = await this.axios.post(roleOrgDelApi, datas)
                    if (res.data.code == 1) {

                        this.getList();
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: "error"
                        });
                    }
                    this.getHash();
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消删除"
                    });
                });
        },

        //获取权限节点数据
        async getInfoData(id) {
            const datas = {
                id: id
            };
            const res = await this.axios.post(roleOrgRangeListApi, datas)
            if (res.data.code == 1) {
                this.nodesData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },

        //获取编辑选中权限节点数据 
        async getRolesData(id) {
            const datas = {
                role_id: id
            };
            const res = await this.axios.post(roleOrgRolesApi, datas)
            if (res.data.code == 1) {
                console.log(res.data);
                this.rolesData = res.data.data.split(',');
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
        //获取列表
        async getList() {
            this.isLoading = true;
            const res = await this.axios.post(roleOrgListApi, {})
            if (res.data.code == 1) {
                this.tableData = res.data.data.data;
                this.defaulted = res.data.data.defaulted;
                this.isLoading = false;
            } else {
                this.isLoading = false;
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
        //获取hash
        async getHash() {
            const datas = {
                page_hash: 'roleManagement'
            };
            const res = await this.axios.post(hashApi, datas)
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
    },
};
</script>

<style lang="scss" scoped>
/deep/.roleManagement .el-form-item__label {
    padding: 0;
}
.roleManagement {
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
            //       .el-button{
            //   border: solid 1px #2f39ff;
            //   color: #2f39ff;
            // }
        }
    }

    .dialogInput {
        display: flex;
        // margin-bottom: 10px;
        .tree_main {
            border: 1px solid #dcdfe6;
            padding: 15px;
            max-height: 200px;
            overflow-y: auto;
        }
        .padd {
            width: 100%;
            margin-right: 20px;
            .paddTit {
                margin-bottom: 10px;
            }
        }

        .flexBox {
            display: flex;
            justify-content: space-between;
            .flex_1 {
                flex: 1;
                &:nth-child(1) {
                    margin-right: 30px;
                }
                .el-date-editor {
                    width: 100%;
                }
            }
        }
    }
    .btn {
        width: 250px;
        // margin-top: 20px;
    }
}
</style>



